export default {
	data() {
		return {
			cid:-1,
			scenicInfo:{}
		}
	},
	onLoad(e) {
		this.cid = e.id;
		this.getScenicInfo();
	},
	methods: {
		getScenicInfo() {
			console.log(6666);
			let that = this;
			that.baseGet( 
				that.U({ c: 'scenic_api', a: 'get_scenic_by_cid',q: {
						cid:that.cid
				}}),
				function(res) {
					that.scenicInfo = res.data
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
	}
}
